@import url(https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;600&family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hidden {
  height: 10vh;
  background-color: #faf6e9;
}

.heading-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #faf6e9;
}

.text-container {
  width: 100%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.heading {
  font-size: 15vw;
  font-weight: 400;
  color: #494949;
  margin: 0;
}

.sub-heading {
  font-size: 2vw;
  font-weight: 300;
  color: #494949;
  margin: 20px 0;
  font-family: "Open Sans", sans-serif;
}

.down-arrow {
  display: flex;
  justify-content: flex-start;
  height: 10vh;
}

.caret-icon {
  color: #494949;
}

.animated {
  position: absolute;
  -webkit-animation: MoveUpDown 1s linear infinite;
          animation: MoveUpDown 1s linear infinite;
}

@-webkit-keyframes MoveUpDown {
  0%,
  100% {
    bottom: 10vh;
  }
  50% {
    bottom: 11.5vh;
  }
}

@keyframes MoveUpDown {
  0%,
  100% {
    bottom: 10vh;
  }
  50% {
    bottom: 11.5vh;
  }
}

.nav-container {
  height: 5vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #faf6e9;
  position: fixed;
  z-index: 100;
}

.icons {
  margin: 10px 10px;
  color: #494949;
}

.nav-links {
  display: flex;
  flex-direction: row;
  color: #494949;
  margin-left: 10px;
  align-items: center;
}

.link-hover {
  cursor: pointer;
}

.iconsEnvelope {
  margin: 3px 10px 0 0;
  color: #494949;
}

.social-links {
  display: flex;
  align-items: center;
}

.about-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ece8d9;
}

.about-title {
  font-size: 10vw;
  margin: 0;
  color: #494949;
}

.about-text {
  width: 60vw;
  font-size: 1.5vw;
  display: inline-block;
  text-align: center;
  color: #494949;
  font-family: 'Open Sans', sans-serif;
}

.caret-icon2 {
  color: #494949;
}

.animated2 {
  position: absolute;
  -webkit-animation: MoveUpDown2 1s linear infinite;
          animation: MoveUpDown2 1s linear infinite;
}

@-webkit-keyframes MoveUpDown2 {
  0%, 100% {
    bottom: -90vh;
  }
  50% {
    bottom: -91.5vh;
  }
}

@keyframes MoveUpDown2 {
  0%, 100% {
    bottom: -90vh;
  }
  50% {
    bottom: -91.5vh;
  }
}
.projects-container {
  height: 80vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-title {
  /* color: #faf6e9; */
}
body {
  font-family: 'Maven Pro', sans-serif;
}

.contact-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #faf6e9;
  color: #494949;
}

.contact-title {
  font-size: 6vw;
  margin-top: 0;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

input {
  font-family: 'Open Sans', sans-serif;
  height: 5vh;
  width: 40vw;
  margin: 10px 0;
  border: 2px solid #494949;
  background-color: #faf6e9;
  font-size: 25px;
}

input::-webkit-input-placeholder {
  text-align: center;
}

input:-ms-input-placeholder {
  text-align: center;
}

input::-ms-input-placeholder {
  text-align: center;
}

input::placeholder {
  text-align: center;
}

textarea {
  font-family: 'Open Sans', sans-serif;
  height: 20vh;
  width: 40vw;
  margin: 10px;
  border: 2px solid #494949;
  background-color: #faf6e9;
  font-size: 25px;
}

textarea::-webkit-input-placeholder {
  text-align: center;
}

textarea:-ms-input-placeholder {
  text-align: center;
}

textarea::-ms-input-placeholder {
  text-align: center;
}

textarea::placeholder {
  text-align: center;
}

button {
  height: 5vh;
  width: 40vw;
  color: #faf6e9;
  font-size: 25px;
  border: 2px solid #494949;
  background-color: #494949
}
.skills-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  background-color: #faf6e9;
}

.skills-title {
  font-size: 10vw;
  margin: 0;
  color: #494949;
  margin-top: 60px;
  margin-bottom: 20px;
}

.icon-container {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-gap: 10px;
  margin: 0 auto;
  text-align: center;
}

.colored {
  font-size: 10vw;
}

